@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;1,100;1,200;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Buda:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cormorant:ital@1&display=swap);
body {
  margin: 0;
  font-family: 'Raleway', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
 .hr50 {
   width: 50%;
   margin: auto;
 }

 .hr50p {
  width: 50px;
  margin: auto;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.paragrafo {
  font-family: 'Buda', cursive;
  text-decoration: none;
}
.bg-white {
  background-color: black;
}
section {
  width: 100%;
}

.box-aula {
  min-height: 250px;
}

li {
  list-style: none;
}
.arrow-teal {
  color: teal;
}
ul {
  margin-left: 20%;
}

.box-aula-titulo {
  min-height: 100px;
}

.relatorios * {
  margin: 5px;
}

.image-aula-nove {
  width: 90px;
  border-radius: 50%;
  height: 90px;
  background-color: teal;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fixed {
  position: fixed;
}

a {
  text-decoration: none;
  color: white;
}
.data-information {
  width: 100%;
  min-height: 150px;
}
.data-information * {
  width: 150px;
  text-align: center;
}

.icon-zoom {
  background-color: #0e71eb;
  color: white;
  padding: 5px;
  border-radius: 15%;
  font-size: 1.3em;
}

a, a:hover {
  color: white;
}
/* footer */
.footer {
  color: lightgoldenrodyellow;
  background-color: #202020;
  text-decoration: none;
  padding-top: 15px;
}

.footer a {
  color: white;
}

.footer-text {
  font-family: 'Buda', cursive;
}

hr {
  background-color: #202020;
  color: white;
}

.copyright {
  padding: 28px 0;
  background-color: #202020;
}

.copyright span,
.copyright a {
  color: #878787;
  transition: all 0.3s linear;
}

.copyright a:hover {
  color: #007bff;
}

.copyright-menu ul {
  text-align: right;
  margin: 0;
}

.copyright-menu li {
  display: inline-block;
  padding-left: 20px;
}

.back-to-top {
  position: relative;
  z-index: 2;
}

.back-to-top .btn-dark {
  width: 35px;
  height: 35px;
  border-radius: 50%;
  padding: 0;
  position: fixed;
  bottom: 20px;
  right: 20px;
  background: #2e2e2e;
  border-color: #2e2e2e;
  display: none;
  z-index: 999;
  transition: all 0.3s linear;
}

.back-to-top .btn-dark:hover {
  cursor: pointer;
  background: #FA6742;
  border-color: #FA6742;
}
.bg-image {
  /* background-image: url('../../arquivosPDF/sermon-mission.jpg'); */
  background: linear-gradient(rgba(255, 255, 255, 0.6), rgba(0, 0, 0, 0.4)), url(/conselhoMissionario/static/media/sermon-mission.979a62e7.jpg);

  min-height: 60vh;
  max-height: 90vh;
  background-size: 100% auto;
  background-position: center;
  background-repeat: no-repeat;
  padding: 3rem 3rem 0rem 3rem;
  opacity: 0.98;
}

.header {
  text-align: center;
}

.header h1 {
  font-family: 'Cormorant', serif;
  color: black;
  /* font-weight: 900; */
  font-size: 6vw;
}

.header p {
  font-family: 'Cormorant', serif;
  color: black;
  /* font-weight: 900; */
  font-size: 4vw;
}

.box-header {
  flex-basis: 40%;
}
@media screen and (max-width: 560px) {
  .bg-image {
    min-height: 25vh;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
    padding: 3rem 3rem 0rem 3rem;
  }

  .header {
    text-align: center;
  }
  
  .header h1 {
    font-family: 'Cormorant', serif;
    color: black;
    /* font-weight: 900; */
    font-size: 8vw;
  }
  
  .header p {
    font-family: 'Cormorant', serif;
    color: black;
    /* font-weight: 900; */
    font-size: 5vw;
  }
}
.image-information {
  margin-top: 5px;
  margin-bottom: 20px;
}
.image-information img {
  width: auto;
  height: 200px;
  border-radius: 5%;
}

.text-information {
  height: 80px;
  vertical-align: center;
  margin: auto;
}

.box-information{
  min-height: 300px;
  opacity: 0.8;
  transition: 0.5s;
  cursor: pointer; 
}

.box-information:hover {
  opacity: 1;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}


